@media (min-width: 768px) and (max-width: 979px) {
.search-area-wrapper {
    background-size: auto;
}
ul.articles .article-entry {
    padding-bottom: 20px;
}

  /* Custom Padding Adjustment For Desktop */
.page-container > .container {
    padding: 40px 20px;
}
#footer-wrapper > #footer {
    padding: 20px 20px;
}
#footer-bottom-wrapper > #footer-bottom {
    padding: 15px 20px 10px;
}
.quick-links-widget .menu > li {
    float: none;
    width: auto;
    margin-right: 0;
}
}
/*------------------------------------------------------------------------------------------------------------------- */
/* Responsive: Header for devices below desktop --------------------------------------------------------------------- */
/*------------------------------------------------------------------------------------------------------------------- */
@media (max-width: 979px) {
  /* logo */
.logo-container {
    text-align: center;
    padding: 10px 0;
}
.logo-container img {
    display: block;
    margin: 10px auto 5px;
}
span.tag-line {
    color: #818a90;
    font-size: 12px;
    display: block;
    text-align: center;
}
.logo-heading {
    display: block;
    position: static;
    top: auto;
    text-align: center;
}

  /* Navigation */
.main-nav {
    position: static;
    top: auto;
    right: auto;
}
.main-nav > div > ul {
    text-align: center;
}
.main-nav > div > ul > li {
    float: none;
    display: inline-block;
}
.main-nav > div > ul > li .sub-menu {
    text-align: left;
    top: 55px;
}
.main-nav > div > ul > li .sub-menu li {
    width: 130px;
}
.main-nav > div > ul > li .sub-menu .sub-menu {
    right: auto;
    left: 150px;
}

  /* Flickr */
.flickr-photos {
    margin-left: -10px;
}
.flickr-photos img {
    width: 67px;
    height: 67px;
}
}
/*------------------------------------------------------------------------------------------------------------------- */
/* Responsive: Landscape phone to desktop/tablet -------------------------------------------------------------------- */
/*------------------------------------------------------------------------------------------------------------------- */
@media (max-width: 767px) {
  /* Navigation */
.main-nav > div {
    display: none;
}
.main-nav .responsive-nav {
    display: block;
    width: 85%;
    margin: 10px auto;
    padding: auto;
    border-radius: 0;
    border: none;
}

  /* Search Area */
.search-area-wrapper {
    background-size: auto;
    min-height: 240px;
    height: auto !important;
    height: 240px;
}
.search-area-wrapper .search-area {
    padding: 20px 0;
}
.search-area-wrapper h3.search-header {
    font-size: 30px;
    padding: 0 15px;
}
.search-area-wrapper p.search-tag-line {
    padding: 0 15px;
    font-size: 14px;
    line-height: 22px;
}
.search-area-wrapper form.search-form input.search-term {
    width: 60%;
}

  /* Comments */
.commentlist li.comment .children {
    margin-left: 0;
}
.social-nav > li {
    float: left;
    margin: 0;
    margin-right: 4px;
}
.hentry .post-meta .like-count {
    float: none;
}
.page-404 h1.title-404 {
    font-size: 100px;
    line-height: 105px;
    margin: 15px;
}
}
/*------------------------------------------------------------------------------------------------------------------- */
/* Responsive: Portrait Phones -------------------------------------------------------------------------------------- */
/*------------------------------------------------------------------------------------------------------------------- */
@media (max-width: 480px) {
  /* Search Area */
.search-area-wrapper form.search-form input.search-term {
    width: 80%;
    display: block;
    margin: 0 auto 10px;
}
.search-area-wrapper form.search-form input.search-btn {
    display: block;
    margin: 0 auto;
}
ul.articles .article-entry {
    padding-bottom: 20px;
}
#jquery-live-search #live-search-results {
    width: 248px;
}
}
/* Menu Fix for resizing to Bigger Screen sizes */
@media (min-width: 980px) {
.main-nav div > ul > li {
    float: none;
    display: inline-block;
}
}
.search_btn {width:50px; margin-top:10px;
}







































































